const state = {
    user: {
        phoneNumber:null,
        providerData:null,
        name:null,
        email:null,
        photoUrl:null,
        uid:null,
        instrument:null,
        ageRange:null,
        skillLevel:null,
        entries:{},
        customPieces:[],
        customStrategies:[]
    },
    updatedSuccess:false
}

import * as firebase from 'firebase'

var database;

  // getters
const getters = {}

// actions
const actions = {
    getUserInfo({ commit }, userInfo) {
      if(!database){
        database = firebase.database()
      }
      database.ref('users/' + userInfo.uid).on("value", snapshot =>{
        let userInf = snapshot.val();
        let userRef = { ...userInfo };
        userRef.instrument = userInf.instrument || '';
        userRef.ageRange = userInf.ageRange || '';
        userRef.skillLevel = userInf.skillLevel || ''; 
        userRef.entries = userInf.entries;
        userRef.customPieces = Object.keys(userInf.customPieces).reduce((cPieces,key)=>{
            cPieces.push({
                id:userInf.customPieces[key].id,
                name:userInf.customPieces[key].name,
                author:userInf.customPieces[key].author || "",
                link:"",
                measures:[],
                notes:"",
                source:userInf.customPieces[key].source || "User Entry",
                strategies:[],
                tempo:"",
                custom:true,
                master:userInf.customPieces[key].master || false
            });
            return cPieces;
        },[]);
        userRef.customStrategies = Object.keys(userInf.customStrategies).map(key=>{
            userInf.customStrategies[key].custom = true;
            return userInf.customStrategies[key];
        });

        Object.keys(userRef.entries).map(key=>{
            Object.keys(userRef.entries[key]).map(key2=>{
                if(key2 !== 'entryName'){
                    if(!userRef.entries[key][key2].measures && !userRef.entries[key][key2].strategies){
                        userRef.entries[key][key2].strategies = [];
                    }
                }
            });
        });

/*         userRef.entries.map(entry=>{
            if(!entry.measures && !entry.strategies){
                entry.strategies = [];
            }
        }); */

        commit('setUser', userRef);
        
        }, err =>{
            console.log('read failed');
        });
    },
    setUserProfile({ commit }, user) {
        database.ref('users/' + user.uid).update({
            instrument: user.instrument,
            ageRange: user.ageRange,
            skillLevel : user.skillLevel
        }).then(()=>{
            commit('setUser', user);
            commit('updateSuccess', true);
            setTimeout(()=>{
                commit('updateSuccess', false);
            },1000);
        });
    },
    setUserInfo({ commit }, user) {
        if(!database){
            database = firebase.database()
        }
        database.ref('users/' + user.uid).update({
            username: user.name,
            email: user.email,
            profile_picture : user.photoUrl
        });
    }
}

// mutations
const mutations = {
    setUser (state, userInfo) {
      state.user = userInfo;
    },
    updateSuccess (state, val) {
        state.updatedSuccess = val;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }