<template>
    <div>
        <Modal v-if="showModal" @close="closeModal">

            <!--
                you can use custom content here to overwrite
                default content
            -->
            <h3 slot="header">{{customStrategy.title}}</h3>
            <textarea slot="body" placeholder="add description" class="form-control" v-model="customStrategy.description"></textarea>

        </Modal>
        <transition name="fade">
            <fieldset>
                <div class="form-top">
                    <div class="form-top-left">
                        <p>Assign Practice Strategies:</p>
                    </div>
                    <div class="form-top-right">
                    </div>
                </div>
                <div class="form-bottom">
                    <div v-for="(piece,index) in selectedPieces" :key="index" class="form-group">
                        <div class="card">
                            <div class="card-header">
                                {{ piece.name + ' - ' + piece.source }}
                            </div>
                            <div class="card-body">
                                <div v-if="!piece.measures || !piece.measures.length">
                                    <div class="row">
                                        <div class="col">
                                            <multiselect
                                                @input="pieceStratUpdate"
                                                placeholder="Select strategy"
                                                :index="index"
                                                track-by="id" 
                                                label="label"
                                                :value="piece.strategies"
                                                :multiple="true"
                                                :taggable="true" 
                                                @tag="addTagPiece"
                                                :options="customStrategies.concat(strategies)">
                                                    <template slot="option" slot-scope="props">
                                                        <div class="option__desc">
                                                            <div style="display:inline-block;">
                                                                <span v-if="props.option.custom" class="option__small">
                                                                    <svg style="height:12px;margin-right:3px;color:#dada43;" aria-hidden="true" data-prefix="fas" data-icon="star" class="svg-inline--fa fa-star fa-w-18" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                                                        <path fill="currentColor" d="M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z"></path>
                                                                    </svg>
                                                                </span>
                                                            </div>
                                                            <div style="display:inline-block;">
                                                                <span class="option__title">
                                                                    {{ props.option.label }}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </template>
                                            </multiselect>
                                            <br />
                                            <p v-if="selectedStrategy[(piece.source === 'User Entry' ? 'U':'')+piece.id]">{{selectedStrategy[(piece.source === 'User Entry' ? 'U':'')+piece.id].description}}</p>    
                                            <br />
                                            <p v-if="!piece.strategies || !piece.strategies.length">No strategies added</p>
                                            <p v-if="piece.strategies && piece.strategies.length">Selected Strategies</p>
                                            <ol>
                                                <li v-for="(strategy,index) in piece.strategies" :key="index">
                                                    {{ strategy.label }}
                                                    <ul v-if="strategy.description">
                                                        <li>{{strategy.description}}</li>
                                                    </ul>
                                                </li>
                                            </ol>                                             
                                            <br />
                                            <br />
                                        </div>
                                    </div>
                                </div>
                                <h5 class="card-title">Measures</h5>
                                <p v-if="!piece.measures || !piece.measures.length">No measures added</p>
                                <div v-for="(measure,indexM) in piece.measures" :key="indexM">
                                    <div class="row">
                                        <div class="col">
                                            <p class="card-text">{{measure.measure}} - Tempo {{measure.tempo}}</p>
                                            <multiselect
                                                @input="measureStratUpdate"
                                                :index="index"
                                                :indexM="indexM"
                                                track-by="id" 
                                                label="label"
                                                :value="measure.strategies"
                                                :multiple="true"
                                                :taggable="true" 
                                                @tag="addTagPiece"
                                                :options="customStrategies.concat(strategies)">
                                                    <template slot="option" slot-scope="props">
                                                        <div class="option__desc">
                                                            <div style="display:inline-block;">
                                                                <span v-if="props.option.custom" class="option__small">
                                                                    <svg style="height:12px;margin-right:3px;" aria-hidden="true" data-prefix="fas" data-icon="star" class="svg-inline--fa fa-star fa-w-18" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                                                        <path fill="currentColor" d="M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z"></path>
                                                                    </svg>
                                                                </span>
                                                            </div>
                                                            <div style="display:inline-block;">
                                                                <span class="option__title">
                                                                    {{ props.option.label }}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </template>                                                
                                            </multiselect>
                                            <br />
                                            <p v-if="selectedStrategy[(piece.source === 'User Entry' ? 'U':'')+piece.id+''+measure.id]">{{selectedStrategy[(piece.source === 'User Entry' ? 'U':'')+piece.id+''+measure.id].description}}</p>    
                                            <br />
                                            <p v-if="!measure || !measure.strategies.length">No strategies added for measure</p>
                                            <p v-if="measure && measure.strategies.length">Selected Strategies</p>
                                            <ol>
                                                <li v-for="(strategy,index) in measure.strategies" :key="index">
                                                    {{ strategy.label }}
                                                    <ul v-if="strategy.description">
                                                        <li>{{strategy.description}}</li>
                                                    </ul>
                                                </li>
                                            </ol>  
                                            <br />
                                            <br />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <button type="button" class="btn go btn-previous" v-on:click="go(2)">Previous</button>
                    <button type="button" class="btn go btn-next" v-on:click="go(4)">Next</button>
                </div>
            </fieldset>
        </transition>
    </div>
</template>

<script>

    import Modal from './Modal.vue';
    import Multiselect from 'vue-multiselect';

    import { mapState, mapActions } from 'vuex';
    

    export default {
    name: 'Step3',
    components: {
        Modal,
        Multiselect
    },
    beforeMount: function(){

    },
    props: {
    },
    data: function(){
        return {
            showModal: false,
            selectedStrategy:{},
            customStrategy : {
                title:'',
                id:'',
                description:'',
                pieceIndex:null,
                measureIndex:null
            }
        }
    },
    computed: mapState({
        selectedPieces: state => state.entry.selectedPieces,
        strategies: state => state.gsheets.practiceStrategies,
        customStrategies: state => state.user.user.customStrategies ? state.user.user.customStrategies.slice(0) : []
    }),
    methods: {
        pieceStratUpdate: function(val){
            let index = event.currentTarget.parentElement.parentElement.parentElement.parentElement.getAttribute('index');
            this.$store.commit('entry/updateSelectedPieceStrat',{index:parseInt(index),strategies:val});
        },
        measureStratUpdate: function(val){
            let index = event.currentTarget.parentElement.parentElement.parentElement.parentElement.getAttribute('index');
            let indexM =  event.currentTarget.parentElement.parentElement.parentElement.parentElement.getAttribute('indexM');
            this.$store.commit('entry/updateSelectedMeasureStrat',{index:parseInt(index),indexM:parseInt(indexM),strategies:val});
        },
        addTagPiece: function(val){
            this.customStrategy.id = this.$parent.uuidv4();
            this.customStrategy.title = val;
            this.customStrategy.pieceIndex = event.currentTarget.parentElement.parentElement.getAttribute('index');
            this.customStrategy.measureIndex = event.currentTarget.parentElement.parentElement.getAttribute('indexM') || null;
            this.showModal = true;
        },
        go: function(ind){
            this.$parent.go(ind);
        },
        addStrategy: function(id,pieceIndex,measureIndex){

            console.log(this.selectedStrategy[id]);

            if(this.selectedStrategy[id]){

                if(!this.selectedStrategy[id].id){
                    this.customStrategy.id = this.$parent.uuidv4();
                    this.customStrategy.refId = id;
                    this.customStrategy.title = this.selectedStrategy[id].label;
                    this.customStrategy.pieceIndex = pieceIndex;
                    this.customStrategy.measureIndex = measureIndex;
                    this.showModal = true;
                }
                else {
                    //add to thing
                    //strategies.push(this.selectedStrategy[id]);
                    if(measureIndex === undefined){
                        this.$store.commit('entry/addToSelectedPieceStrat',{index:pieceIndex,strat:this.selectedStrategy[id]});
                    }
                    else {
                        this.$store.commit('entry/addToSelectedMeasureStrat',{index:pieceIndex,indexM:measureIndex,strat:this.selectedStrategy[id]});
                    }
                    delete this.selectedStrategy[id];
                }
            }
        },
        handleNewEntry: function(option){
            return Object.keys(option).reduce((name,key)=>{
                name += option[key];
                return name;
            },"");
        },
        closeModal: function(){
            if(this.customStrategy.description){
                let strat = {
                    label:this.customStrategy.title,
                    id:this.customStrategy.id,
                    description:this.customStrategy.description,
                    custom:true
                };
                if(!this.customStrategy.measureIndex){
                    this.$store.commit('entry/addToSelectedPieceStrat',{
                        index:this.customStrategy.pieceIndex,
                        strat:strat
                    });
                }
                else {
                    this.$store.commit('entry/addToSelectedMeasureStrat',{
                        index:this.customStrategy.pieceIndex,
                        indexM:this.customStrategy.measureIndex,
                        strat:strat
                    });
                }
                this.$store.commit('gsheets/addToPracticeStrategies',strat);
                this.customStrategy.description = '';
            }
            this.showModal = false;
        }
    }
}
</script>