const state = {
    login:false,
    newEntry:false,
    profile:false,
    home:true,
    admin:false,
    entryUID:'',
    existingEntry:''
}

  // getters
const getters = {}

// actions
const actions = {}

// mutations
const mutations = {
    exitEntry(state){
      state.newEntry = false;
      state.entryUID = '';
      state.existingEntry = '';
      state.home = true;
    },
    setLogin (state, val) {
      state.login = val;
    },
    setNewEntry (state, val) {
      state.newEntry = val;
    },
    setProfile (state, val) {
      state.profile = val;
    },
    setHome (state, val) {
      state.home = val;
    },
    setAdmin(state, val) {
      state.admin= val;
    },
    setEntryUID (state, val) {
      state.entryUID = val;
    },
    setExistingEntry (state, val) {
      state.existingEntry = val;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }