const state = {
    musicPieces:[],
    practiceStrategies:[]
}

// getters
const getters = {}

// actions
const actions = {
    getMusicPieces({ commit }) {

        if(!state.musicPieces.length){

            let pieces, sources, sourceMap = {},
            musicPieces = {
                map:{},
                rows:[]
            };

            var xhttp = new XMLHttpRequest();
            xhttp.onreadystatechange = function() {
                if (this.readyState == 4 && this.status == 200) {
                    let data = JSON.parse(xhttp.responseText);
    
                    sources = data.valueRanges[1].values;
                    sources.shift();
                    sourceMap = sources.reduce((prev,row)=>{
                        prev[row[1].toString()] = row[0];
                        return prev;
                    },sourceMap);
    
                    pieces = data.valueRanges[0].values;
                    pieces.shift();
                    musicPieces = pieces.sort(function (x, y) {
                        if(x[0] < y[0]) return -1;
                        if(x[0] > y[0]) return 1;
                    
                        return x[2] - y[2];
                    }).reduce((prev,row)=>{
                        prev.map[row[1]] = {
                            sourceID:row[2]
                        };
                        prev.rows.push({name:row[0],source:sourceMap[row[2]],tempo:'',measures:[],strategies:[],id:row[1],link:'',notes:''});
                        return prev;
                    },musicPieces);
                    // Try to get custom pieces from firebase
                    commit('setMusicPieces', musicPieces);
                }
    
            };
    
            //xhttp.open("GET", "https://sheets.googleapis.com/v4/spreadsheets/1uVMJcnjaxyz8_w6xrR5AFiQ8zMktKuOPEsLL3q7YofM/values:batchGet?ranges=Piece!A1:C10&ranges=Source!A:B&key=AIzaSyA251gYOA-3nYb0uOHRvdeF5f-zX2PhmpA", true);
            
            xhttp.open("GET", "https://sheets.googleapis.com/v4/spreadsheets/1uVMJcnjaxyz8_w6xrR5AFiQ8zMktKuOPEsLL3q7YofM/values:batchGet?ranges=Piece!A:C&ranges=Source!A:B&key=AIzaSyA251gYOA-3nYb0uOHRvdeF5f-zX2PhmpA", true);
            
            //values:batchGet?ranges=Source!A:B&ranges=Source!A:B
            ///values/Piece!A:B,Source!A:B?key=AIzaSyA251gYOA-3nYb0uOHRvdeF5f-zX2PhmpA
            xhttp.send();

        }
    },
    getPracticeStrategies({ commit }) {

        if(!state.practiceStrategies.length){

            let strategies;

            var xhttp = new XMLHttpRequest();
            xhttp.onreadystatechange = function() {
                if (this.readyState == 4 && this.status == 200) {
                    let data = JSON.parse(xhttp.responseText);
                    strategies = data.valueRanges[0].values;
                    strategies.shift();
                    strategies = strategies.sort(function (x, y) {
                        if(x[0] < y[0]) return -1;
                        if(x[0] > y[0]) return 1;
                    
                        return 0;
                    }).reduce((prev,row)=>{
                        prev.push({label:row[0],id:row[1],description:row[2] || ''});
                        return prev;
                    },[]);
                    commit('setPracticeStrategies',strategies);
                }
            };            
            xhttp.open("GET", "https://sheets.googleapis.com/v4/spreadsheets/1SOvyvvAGUuniEf2EXT0jSk4v0cDdq_mxIFRmH1Tvilk/values:batchGet?ranges=PS!A:C&key=AIzaSyA251gYOA-3nYb0uOHRvdeF5f-zX2PhmpA", true);
            xhttp.send();

        }

    }
}

// mutations
const mutations = {
    setMusicPieces(state, musicPieces) {
      state.musicPieces = musicPieces;
    },
    setPracticeStrategies(state, practiceStrategies) {
        state.practiceStrategies = practiceStrategies;
    },
    addToPracticeStrategies(state, strategy){
        state.practiceStrategies.push(strategy);
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }