<template>
    <div class="login">
        <transition name="fade">
            <fieldset>
                <p>Login</p>
                <div id="firebaseui-auth-container"></div>
            </fieldset>
        </transition>
    </div>
</template>
<script>

//import * as firebase from 'firebase' //auth,database
import {auth as fireAuth,database as fireDB} from 'firebase' //auth,database
import * as firebaseui from 'firebaseui'

export default {
  name: 'Login',
  data: function(){
      return {
        uiConfig:{
            signInSuccessUrl: 'http://localhost:8080/',
            signInOptions: [
            // Leave the lines as is for the providers you want to offer your users.
            fireAuth.GoogleAuthProvider.PROVIDER_ID,
            fireAuth.FacebookAuthProvider.PROVIDER_ID
            /*    
                firebase.auth.TwitterAuthProvider.PROVIDER_ID,
                firebase.auth.GithubAuthProvider.PROVIDER_ID,
                firebase.auth.EmailAuthProvider.PROVIDER_ID,
                firebase.auth.PhoneAuthProvider.PROVIDER_ID 
            */
            ],
            // Terms of service url.
            tosUrl: ''
        }
      };
  },
  beforeMount: function(){
    let vueThis = this,
        userInfo = {};

    //fireAuth().signOut();
    
    var db = fireDB();

    fireAuth().onAuthStateChanged(function(user) {
      if (user) {
        // User is signed in.
        userInfo.phoneNumber = user.phoneNumber;
        userInfo.providerData = user.providerData;
        userInfo.name = user.displayName;
        userInfo.email = user.email;
        userInfo.photoUrl = user.photoURL;
        userInfo.uid = user.uid;

        vueThis.$store.dispatch('user/setUserInfo',userInfo);
        vueThis.$store.dispatch('user/getUserInfo',userInfo);

        vueThis.$store.commit('container/setLogin', false);
        vueThis.$emit('update',{ref:'database',val:db});

        /*             
            user.getIdToken().then(function(accessToken) {
                debugger;
                console.log(accessToken);
            }); 
        */

      } else {
        var ui = new firebaseui.auth.AuthUI(auth());
        // The start method will wait until the DOM is loaded.
        ui.start('#firebaseui-auth-container', vueThis.uiConfig);
        vueThis.$store.commit('container/setLogin', true);
      }
    }, function(error) {
      console.log(error);
    });
  }
}
</script>
