const state = {
    selectedPieces:[],
    updated:false
}

import * as firebase from 'firebase'

var database;

  // getters
const getters = {}

// actions
const actions = {
    updateUserEntry({ commit, rootState }, info) {
        if(!database){
            database = firebase.database();
        }

        let strategies = 
            info.userEntries.strategies
            .reduce((strategyObj,strategy)=>{
                strategyObj[strategy.id] = strategy;
                return strategyObj;
            },{});

        let pieces = 
            info.userEntries.pieces
            .reduce((piecesObj,piece)=>{
                piecesObj[piece.id] = piece;
                return piecesObj;
            },{});

        //get unique id based on string, set child key or set
        database.ref('users/' + info.userUID + '/customPieces/').update(
            pieces
        );
        database.ref('users/' + info.userUID + '/customStrategies/').update(
            strategies
        )/*.then(()=>{
            
        },(err)=>{
          debugger;  
        })*/;


        commit('setUpdated',true);
        setTimeout(()=>{
            commit('setUpdated',false);
        },1000);


    },
    updateEntry({ commit }, entry){
        if(!database){
            database = firebase.database();
        }
        database.ref('users/' + entry.userUID + '/entries/' + entry.entryUID).set(
            entry.selectedPieces
        ).then(()=>{
            commit('container/exitEntry',null,{root:true});
        },(err)=>{
            console.log(err);
            alert('err saving, please contact support.');  
        });
    },
    addEntry({ commit }, entry){
        if(!database){
            database = firebase.database();
        }
        let newEntryRef = database.ref('users/' + entry.userUID + '/entries').push();
        newEntryRef.set(entry.selectedPieces).then(()=>{
            commit('container/exitEntry',null,{root:true});
        },(err)=>{
            console.log(err);
            alert('err saving, please contact support.');  
        });
    },
    removeCustomEntry({ commit }, entry){
        if(!database){
            database = firebase.database();
        }
        database.ref('users/' + entry.userUID + '/' + entry.custom + '/' + entry.entryUID).set(null);
    }
}

// mutations
const mutations = {
    setSelectedPieces (state, selectedPieces) {
      state.selectedPieces = selectedPieces;
    },
    addToSelectedPieces(state, selectedPiece){
        state.selectedPieces.push(selectedPiece);
    },
    setEntryName(state, name){
      state.selectedPieces.entryName = name;
    },
    addToSelectedPieceStrat(state,info){
        state.selectedPieces[info.index].strategies.push(info.strat);
    },
    addToSelectedMeasureStrat(state,info){
        state.selectedPieces[info.index].measures[info.indexM].strategies.push(info.strat);
    },
    updatePieceTempo (state,info) {
        state.selectedPieces[info.ind].tempo = info.tempo;
    },
    addToMeasure(state,info){
        info.measure.strategies = [];
        info.measure.id = state.selectedPieces[info.index].measures.length;
        state.selectedPieces[info.index].measures.push(info.measure);
    },
    updateSelectedPieces(state,val){
        state.selectedPieces = val;
    },
    updateSelectedPieceStrat(state,info){
        state.selectedPieces[info.index].strategies = info.strategies;
    },
    updateSelectedMeasureStrat(state,info){
        state.selectedPieces[info.index].measures[info.indexM].strategies = info.strategies;
    },
    setName(state,name){
        state.selectedPieces.entryName = name;
    },
    setNotes(state,info){
        state.selectedPieces[parseInt(info.index)].notes = info.notes;
    },
    setLink(state,info){
        state.selectedPieces[parseInt(info.index)].link = info.link;
    },
    setUpdated(state,val){
        state.updated = val;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }

