<template>
    <div>
        <transition name="fade">
            <fieldset>
                <div class="form-top">
                    <div class="form-top-left">
                        <p>Assign Measures:</p>
                    </div>
                    <div class="form-top-right">
                    </div>
                </div>
                <div class="form-bottom">
                    <div v-for="(piece,index) in selectedPieces" :key=index class="form-group">
                        <div class="card">
                            <div class="card-header">
                                {{ piece.name + ' - ' + piece.source }}
                            </div>
                            <div class="card-body">
                                <div class="input-group mb-3">
                                    <input type="text" class="form-control" placeholder="Enter Measure">
                                    <input type="text" class="form-control" placeholder="Enter Tempo">
                                    <div class="input-group-append">
                                        <button class="btn btn-outline-secondary" type="button" v-on:click="addMeasure(index,$event)">Add Measure</button>
                                    </div>
                                </div>
                                <h5 class="card-title">Measures</h5>
                                <p v-if="!piece.measures || !piece.measures.length">No measures added</p>
                                <p v-for="(measure,index) in piece.measures" :key=index class="card-text">{{measure.measure}} - Tempo {{measure.tempo}}</p>
                            </div>
                        </div>
                    </div>
                    <button type="button" class="btn go btn-previous" v-on:click="go(1)">Previous</button>
                    <button type="button" class="btn go btn-next" v-on:click="go(3)">Next</button>
                </div>
            </fieldset>
        </transition>
    </div>
</template>

<script>

    import { mapState, mapActions } from 'vuex';

    export default {
    name: 'Step2',
    beforeMount: function(){

    },
    props: {
    },
    data: function(){
        return {
        }
    },
    computed: mapState({
        selectedPieces: state => state.entry.selectedPieces
    }),
    methods: {
        go: function(ind){
            this.$parent.go(ind);
        },
        addMeasure: function(index,ev){
            var tempoInput = ev.target.parentElement.previousElementSibling,
                measureInput = tempoInput.previousElementSibling;

            if(measureInput.value.trim() && tempoInput.value.trim()){
                this.$store.commit('entry/addToMeasure',{
                    index:index,
                    measure:{measure:measureInput.value,tempo:tempoInput.value}
                });
                measureInput.value = "";
                tempoInput.value = "";
            }
        }
    }
}
</script>