<template>
    <div class="home">
        <br />
        <ul class="nav nav-tabs">
            <li class="nav-item" @click="selection='entries'">
                <a class="nav-link" :class="{active:selection==='entries'}" href="#">Entries</a>
            </li>
            <li class="nav-item" @click="selection='customPieces'">
                <a class="nav-link" :class="{active:selection==='customPieces'}" href="#">Custom Pieces</a>
            </li>
            <li class="nav-item" @click="selection='customStrategies'">
                <a class="nav-link" :class="{active:selection==='customStrategies'}" href="#">Custom Strategies</a>
            </li>
        </ul>
        <br />
            <div class="card" v-if="selection ==='entries'">
                <div class="card-header">
                    Entries
                </div>
                <div class="card-body">
                    <div v-for="(entry,i) in entries" :key="i">
                        <a v-on:click="goToEntry(i,$event)" href="">{{entry.entryName}}</a>
                    </div>
                </div>
            </div>
            <div class="card" v-if="selection ==='customPieces'">
                <div class="card-header">
                    Add/Edit Custom Pieces
                </div>
                <div class="card-body">
                    <div class="row">
                        <div class="col">
                            <select v-model="piece" class="form-control" @change="pieceSelect">
                                <option value="">
                                    Add New Custom Piece
                                </option>
                                <option :value="piece.name" v-for="(piece,index) in customPieces" :ind="index" :key="index">
                                    {{piece.name}}
                                </option>
                            </select>
                        </div>
                    </div>
                    <br />
                    <div>
                        <div class="row">
                            <div class="col">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text">Name</span>
                                    </div>
                                    <input id="pieceName" type="text" :value="customPieces[pieceIndex] ? customPieces[pieceIndex].name : ''" class="form-control">
                                </div>
                            </div>
                            <div class="col">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text">Source</span>
                                    </div>
                                    <input id="pieceSource" type="text" :value="customPieces[pieceIndex] ? customPieces[pieceIndex].source : ''" class="form-control">
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text">Author</span>
                                    </div>
                                    <input id="pieceAuthor" type="text" :value="customPieces[pieceIndex] ? customPieces[pieceIndex].author : ''" class="form-control">
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col">
                                <div class="form-check form-check-inline">
                                    <input id="pieceMaster" class="form-check-input" type="checkbox" :checked="customPieces[pieceIndex] ? customPieces[pieceIndex].master : false" :value="customPieces[pieceIndex] ? customPieces[pieceIndex].master : false">
                                    <label for="pieceMaster" class="form-check-label">Master List Submission</label>
                                </div>
                            </div>
                            <transition name="fade">
                                <div v-if="updated" class="alert alert-success" role="alert">
                                    {{(piece !== '') ? 'Updated!' : 'Added!'}}
                                </div>
                            </transition>
                            <div class="col">
                                <button @click="updatePiece" class="btn btn-success">{{(piece !== '') ? 'Update ' : 'Add '}} Piece</button>
                            </div>
                            <div v-if="piece" class="col">
                                <button @click="removeEntry('customPieces',customPieces[pieceIndex].id)" class="btn btn-danger">Remove Piece</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="card" v-if="selection ==='customStrategies'">
                <div class="card-header">
                    Add/Edit Custom Strategies
                </div>
                <div class="card-body">
                    <div class="row">
                        <div class="col">
                            <select v-model="strategy" class="form-control" @change="strategySelect">
                                <option value="">
                                    Add New Custom Strategy
                                </option>
                                <option :value="strategy.label" :ind="index" v-for="(strategy,index) in customStrategies" :key="index">
                                    {{strategy.label}}
                                </option>
                            </select>
                        </div>
                    </div>
                    <br />
                    <div>
                        <div class="row">
                            <div class="col">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text">Name</span>
                                    </div>
                                    <input id="stratLabel" type="text" :value="customStrategies[strategyIndex] ? customStrategies[strategyIndex].label : ''" class="form-control">
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text">Description</span>
                                    </div>
                                    <textarea id="stratDescription" :value="customStrategies[strategyIndex] ? customStrategies[strategyIndex].description : ''" class="form-control"></textarea>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col">
                                <div class="form-check form-check-inline">
                                    <input id="stratMaster" class="form-check-input" type="checkbox" :checked="customStrategies[strategyIndex] ? customStrategies[strategyIndex].master : false" :value="customStrategies[strategyIndex] ? customStrategies[strategyIndex].master : false">
                                    <label for="stratMaster" class="form-check-label">Master List Submission</label>
                                </div>
                            </div>
                            <transition name="fade">
                                <div v-if="updated" class="alert alert-success" role="alert">
                                    {{(strategy !== '') ? 'Updated!' : 'Added!'}}
                                </div>
                            </transition>
                            <div class="col">
                                <button @click="updateStrategy" class="btn btn-success">{{(strategy !== '') ? 'Update ' : 'Add '}}  Strategy</button>
                            </div>
                            <div v-if="strategy" class="col">
                                <button @click="removeEntry('customStrategies',customStrategies[strategyIndex].id)" class="btn btn-danger">Remove Strategy</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        <br />
        <br />
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Header',
  props: {
      entries:Object,
      customPieces:Array,
      customStrategies:Array,
      updated:Boolean
  },
  data: function(){
    return {
        selection:'entries',
        strategyIndex:"",
        pieceIndex:"",
        piece:"",
        strategy:""
    }
  },
  computed: mapState({
        userUID: state => state.user.user.uid
  }),
  beforeMount: function(){
  },
  methods:{
      removeEntry(custom,entryId){
          this.$store.dispatch('entry/removeCustomEntry',{userUID:this.userUID,custom:custom,entryUID:entryId})
          .then(()=>{
            if(custom === 'customPieces'){
                this.piece = "";  
                this.pieceIndex = "";
            }
            if(custom === 'customStrategies'){
                this.strategy = "";
                this.strategyIndex = "";
            }
          });
      },
      updateStrategy(){
          let label = document.getElementById('stratLabel').value;
          if(label){
            let id = this.strategyIndex ? this.customStrategies[this.strategyIndex].id : this.uuidv4();
            let updateStrat = {
                id:id,
                label:label,
                description:document.getElementById('stratDescription').value,
                master:document.getElementById('stratMaster').checked
            };
            let userEntries = {
                strategies:[updateStrat],
                pieces:[]
            };
            this.$store.dispatch('entry/updateUserEntry',{userUID:this.userUID,userEntries:userEntries})
            .then(()=>{
/*                 setTimeout(()=>{
                    this.strategy = label;
                },1400); */
            });
          }
      },
      updatePiece(){
        let name = document.getElementById('pieceName').value;
        if(name){
            let id = this.pieceIndex ? this.customPieces[this.pieceIndex].id : this.uuidv4();
            let updatePiece = {
                id:id,
                name:name,
                source:document.getElementById('pieceSource').value,
                author:document.getElementById('pieceAuthor').value,
                master:document.getElementById('pieceMaster').checked
            };
            let  userEntries = {
                strategies:[],
                pieces:[updatePiece]
            };
            this.$store.dispatch('entry/updateUserEntry',{userUID:this.userUID,userEntries:userEntries})
            .then(()=>{
/*                 setTimeout(()=>{
                    this.piece = name;
                },1400); */
            });
        }
      },
      strategySelect(ev){
          let index = ev.currentTarget.options[ev.currentTarget.options.selectedIndex].getAttribute('ind');
          if(index){
            this.strategyIndex = parseInt(index);
          }
          else {
              this.strategyIndex = "";
          }
      },
      pieceSelect(ev){
          let index = ev.currentTarget.options[ev.currentTarget.options.selectedIndex].getAttribute('ind');
          if(index){
            this.pieceIndex = parseInt(index);
          }
          else {
              this.pieceIndex = "";
          }
      },
      goToEntry(entryRef,ev){
            ev.preventDefault();
            this.$store.commit('container/setExistingEntry',this.entries[entryRef]);
            this.$store.commit('container/setEntryUID',entryRef);
            this.$store.commit('container/setHome',false);
            this.$store.commit('container/setNewEntry',true);
      },
      uuidv4() {
        return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, c =>
            (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
        ) 
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .nav-tabs .nav-item a {
        color:#a0a3a7;
    }
    .nav-tabs .nav-item a:hover {
        color:#202127;
    }
</style>
