<template>
    <div>
        <transition name="fade">
            <fieldset v-if="step1_a">
                <div class="form-top">
                    <div class="form-top-left">
                        <p>Select Piece(s):</p>
                    </div>
                    <div class="form-top-right">
                    </div>
                </div>
                <div class="form-bottom">
                    <multiselect 
                        :value="selectedPieces" 
                        placeholder="Select Piece" 
                        label="name" 
                        track-by="id" 
                        :multiple="true"
                        :taggable="true"
                        @tag="addTag"
                        @input="checkInput"
                        :options="customPieces.concat(rows)">
                        <template slot="option" slot-scope="props">
                            <div class="option__desc">
                                <div style="display:inline-block;">
                                    <span v-if="props.option.source === 'User Entry' || props.option.custom" class="option__small">
                                        <svg style="height:12px;margin-right:3px;color:#dada43;" aria-hidden="true" data-prefix="fas" data-icon="star" class="svg-inline--fa fa-star fa-w-18" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                            <path fill="currentColor" d="M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z"></path>
                                        </svg>
                                    </span>
                                </div>
                                <div style="display:inline-block;">
                                    <span class="option__title">
                                        {{ props.option.name }}
                                    </span>
                                </div>
                            </div>
                        </template>
                    </multiselect>
                    <br />
                    <p v-if="selectedPieces.length">Selected Pieces</p>
                    <ol>
                        <li v-for="(piece,i) in selectedPieces" :key="i">
                            {{ piece.name }} <span class="text-muted small"> - {{ piece.source }}</span>
                        </li>
                    </ol>
                    <br />
                    <br />
                    <br />
                    <button type="button" v-bind:class="{disabled:!selectedPieces.length}" :disabled="!selectedPieces.length" class="go btn btn-next" v-on:click="go('1_b')">Next</button>
                </div>
            </fieldset>
        </transition>
        <transition name="fade">
            <fieldset v-if="step1_b">
                <div class="form-top">
                    <div class="form-top-left">
                        <p>Set Tempo(s):</p>
                    </div>
                    <div class="form-top-right">
                    </div>
                </div>
                <div class="form-bottom">
                    <div v-for="(piece,i) in selectedPieces" :key="i" class="form-group">
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <span class="input-group-text" id="basic-addon1">
                                    {{ piece.name+' ' }} <span class="text-muted small"> - {{ piece.source }}</span>
                                </span>
                            </div>
                            <input type="text" :data-index="i" :value="piece.tempo" class="form-control" placeholder="Enter Tempo" @change="updateTempo">
                        </div>
                        <span>{{piece.temp}}</span>
                    </div>
                    <br />
                    <br />
                    <button type="button" class="btn go btn-previous" v-on:click="go('1_a')">Previous</button>
                    <button type="button" class="btn go btn-next" v-on:click="go(2)">Next</button>
                </div>
            </fieldset>
        </transition>
    </div>
</template>

<script>

    import { mapState } from 'vuex';
    import Multiselect from 'vue-multiselect';

    export default {
    name: 'Step1',
    components: { Multiselect },
    beforeMount: function(){
    },
    props: {
    },
    data: function(){
        return {
            step1_a:true,
            step1_b:false
        }
    },
    computed: mapState({
        selectedPieces: state => state.entry.selectedPieces,
        rows: state => state.gsheets.musicPieces.rows ? state.gsheets.musicPieces.rows.slice(0) : [],
        customPieces: state => state.user.user.customPieces ? state.user.user.customPieces.slice(0) : []
        //TODO: Check for existing v-models - invalid mutations.
    }),
    methods: {
        customLabel ({ name, id }) {
            return `${name} – ${id}`
        },
        handleNewEntry(option){
            return this.$parent.handleNewEntry(option);
        },
        updateTempo: function(ev){
            let ind = parseInt(ev.target.getAttribute('data-index'));
            this.$store.commit('entry/updatePieceTempo',{ind:ind,tempo:ev.target.value});
        },
        addTag: function(val){
            let entry = {
                name:val,
                source:'User Entry',
                tempo:'',
                measures:[],
                strategies:[],
                id:this.$parent.uuidv4()
            };
            this.rows.push(entry);
            this.$store.commit('entry/addToSelectedPieces',
                entry
            );
        },
        checkInput: function(val){
            if(!val){
                return false;
            }
            this.$store.commit('entry/updateSelectedPieces',val);
        },
        go: function(ind){
            if(ind==='1_b'){
                this['step1_a'] = false;
                setTimeout(()=>{
                    this['step1_b'] = true;   
                },500); 
            }
            else if(ind==='1_a'){
                this['step1_b'] = false;
                setTimeout(()=>{
                    this['step1_a'] = true;   
                },500);     
            }
            else {
                this.$parent.go(ind);
            }

        }
    }
}
</script>
