<template>
  <div id="mymusicmastery">
    <Header :login="login" :newEntry="newEntry" :profile="profile" :home="home" :admin="admin" @update="updateData" />
    <div class="container">
<!--     <img src="./assets/logo.png"> -->
<!--     <HelloWorld msg="Welcome to Your Vue.js App"/> -->
      <Login v-show="login" @update="updateData"/>
      <div v-if="!login">
        <Steps @update="updateData" v-if="newEntry" :entryUID="entryUID"  :database="database" :userUID="user.uid" :existingEntry="existingEntry" />
        <Profile v-if="profile" :user="user" />
        <Home v-if="home" :entries="user.entries" :customPieces="user.customPieces" :customStrategies="user.customStrategies" :updated="updated" />
        <Admin v-if="admin" />
      </div>
    </div>
  </div>
</template>

<script>
import Login from './components/Login.vue';
import Steps from './components/steps/Steps.vue';
import Profile from './components/profile/Profile.vue';
import Admin from './components/admin/Admin.vue';
import Header from './components/Header.vue';
import Home from './components/home/Home.vue';

import { mapState, mapActions } from 'vuex';

export default {
  name: 'mymusicmastery',
  components: {
    Login,
    Steps,
    Profile,
    Header,
    Home,
    Admin
  },
  created: function(){
  },
  data: function(){
    return {
      existingEntry:null,
      entryUID:''
    }
  },
  computed: mapState({
    user: state => state.user.user,
    login: state => state.container.login,
    profile: state => state.container.profile,
    newEntry: state => state.container.newEntry,
    admin: state => state.container.admin,
    home: state => state.container.home,
    updated: state=> state.entry.updated
  }),
  methods: {
    updateData(arg){
      this[arg.ref] = arg.val;
    }
  }
}
</script>

