<template>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <a class="navbar-brand" href="#" v-on:click="updateNav(false,false,true)">My Music Mastery</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
          <li class="nav-item" v-bind:class="{ active: home }">
            <a class="nav-link" href="#" v-bind:class="{ disabled: login }" v-on:click="updateNav(false,false,true,false)">Home</a>
          </li>
          <li class="nav-item" v-bind:class="{ active: newEntry }">
            <a v-bind:class="{ disabled: login }" v-on:click="updateNav(true,false,false,false)" class="nav-link" href="#">{{entryUID ? 'Existing' : 'New'}} Entry</a>
          </li>
          <li class="nav-item" v-bind:class="{ active: profile }">
            <a v-bind:class="{ disabled: login }" v-on:click="updateNav(false,true,false,false)" class="nav-link" href="#">Profile</a>
          </li>
          <li class="nav-item" v-bind:class="{ active: admin }">
            <a v-bind:class="{ disabled: login }" v-on:click="updateNav(false,false,false,true)" class="nav-link" href="#">Admin</a>
          </li>
        </ul>
      </div>
    </nav>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: 'Header',
  props: {
  },
  beforeMount: function(){
  },
  computed: mapState({
    login: state => state.container.login,
    newEntry: state => state.container.newEntry,
    profile: state => state.container.profile,
    home: state => state.container.home,
    admin: state => state.container.admin,
    entryUID: state => state.container.entryUID
  }),
  methods:{
      updateNav(newEntry,profile,home,admin){
        this.$store.commit('container/setNewEntry',newEntry);
        this.$store.commit('container/setProfile',profile);
        this.$store.commit('container/setHome',home);
        this.$store.commit('container/setAdmin',admin);
      }
  }
}
</script>

